@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #eae9e5;
}







nav {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  /* display: flex;
  justify-content: space-around;
  align-items: center; */
  /* padding: 20px 0; */
  background-color: #efede9;
  z-index: 10000;
}

nav .nav-bar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px 0;
  background-color: #efede9;
  z-index: 10000;
}

nav a {
  color: #6a6867;
  display: block;
  padding: 0.5em 1em;
  text-decoration: none;
}
nav ul {
  display: flex;
}

nav ul li {
  margin: 0 5px;
  list-style-type: none;
  border-bottom: 2px solid transparent;
}

nav ul li:hover {
  border-bottom: 2px solid #888dc4;
}

nav ul li a {
  color: #6a6867;
}

nav ul li:hover a {
  color: #888dc4;
}

nav a:active {
  color: #888dc4;
}

.sub-menu-parent {
  display: inline-block;
  position: relative;
}

.sub-menu {
  flex-direction: column;
  visibility: hidden; /* hides sub-menu */
  opacity: 0;
  position: absolute;
  -webkit-transform: translateY(-2em);
          transform: translateY(-2em);
  transition: all 1s;
  background: #ddd;
  min-width: 140px;
}

.sub-menu-parent:hover .sub-menu {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

/* presentational */

nav .logo-menu {
  display: flex;
  align-items: center;
}

nav .logo-menu .drawer {
  display: none;
}

nav .logo-menu .logo-title {
  padding: 0 50px;
  font-size: 20px;
  font-weight: 700px;
}

nav .Icons-view {
  display: flex;
  align-items: center;
  position: relative;
}

nav .Icons-view .icon {
  margin: 0 5px;
}

nav .Icons-view .icon-view {
  padding: 5px;
}

nav .menu-icon:hover {
  color: #6d6d6d;
}

nav .menu-icon:hover {
  color: #513992;
}
/* SSearch Bar */
nav .Icons-view input[type="text"] {
  width: 120px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 20px;
  font-size: 16px;
  background-color: white;
  padding: 10px 10px 10px 40px;
  transition: width 0.4s ease-in-out;
}

nav .Icons-view input[type="text"]:focus {
  outline: none !important;
  box-shadow: 0 0 10px #719ece;
  width: 200px;
  border: 1px solid #ccc;
}

nav .Icons-view .search-icon {
  position: absolute;
  left: 20px;
  top: 15px;
  z-index: 1000;
  cursor: pointer;
}
/* SSearch Bar */

/* Dashboard page styles */
.body-container .toast-container {
  z-index: 21000;
}

