/* css Drawer navigation */
nav .drawer-navigation {
  background-color: #060b26;
  width: 300px;
  position: absolute;
  top: 0;
  left: -100%;
  height: 100vh;
  transition: 850ms;
}
.drawer-navigation-active {
  background-color: #060b26;
  width: 300px;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  transition: 400ms;
  overflow-y: scroll;
}
nav .drawer-close-icon {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 2% 4%;
}
nav .drawer-menu-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

nav .drawer-menu-container .drawer-menu-list {
  width: 80%;
  align-items: center;
}
nav .drawer-menu-container .drawer-menu-list .before-drawer-icon-text {
  margin: 10px 0 0 0;
}
nav
  .drawer-menu-container
  .drawer-menu-list
  .before-drawer-icon-text
  .before-drawer-icon-text-login {
  margin: 0 0 10px 0;
}
nav .drawer-menu-container .drawer-menu-list .drawer-icon-text p {
  color: #ffffff;
  padding: 0 10px;
}

nav .drawer-menu-container .drawer-menu-list .drawer-icon-text {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  text-decoration: none;
  font-size: 1.2em;
  color: #fff;
}
nav .drawer-menu-container .drawer-menu-list ul {
  flex-direction: column;
}

nav .drawer-menu-container .drawer-menu-list ul li:hover {
  border-bottom: 2px solid transparent;
}

nav .drawer-menu-container .drawer-menu-list ul li:hover {
  background-color: #7180df;
  border-bottom: 2px solid transparent;
  border-radius: 5px;
}

nav .drawer-menu-container .drawer-menu-list ul .category-sub-menu {
  margin-left: 15px;
}

nav
  .drawer-menu-container
  .drawer-menu-list
  ul
  .category-sub-menu
  .before-drawer-icon-text-sub {
  margin: 5px 0 0 0;
}

nav
  .drawer-menu-container
  .drawer-menu-list
  ul
  .category-sub-menu
  .drawer-icon-text
  p {
  font-size: 14px;
}

