.animation-text {
  background-color: red;
  height: 100px;
  width: 100px;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: width 2s;
}

.animation-text2 {
  background-color: green;
  -webkit-animation: example 4s;
          animation: example 4s;
}

/* Dashboard page styles */

/* dummy animation text styles */
@-webkit-keyframes AnimationText {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px);
  }
}
@keyframes AnimationText {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px);
  }
}
/* dummy animation text styles */
/* body style */
.body-container {
  padding-bottom: 20px;
  position: relative;
}

/* Header slider  */
.image-text-container {
  width: 100%;
  height: 500px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
.image-text {
  display: flex;
  height: 100%;
  width: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.8s;
}

.image-text-anime {
  display: flex;
  height: 100%;
  width: 100%;
  position: absolute;
  opacity: 1;
  transition: opacity ease-in-out 1s;
}

.text-div {
  width: 45%;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  z-index: 1000;
}
.animation-text-div {
  -webkit-animation: AnimationText 2s ease-in;
          animation: AnimationText 2s ease-in;
}

@-webkit-keyframes imageAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes imageAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.image-div {
  width: 55%;
  -webkit-animation: imageAnimation 4s ease-in;
          animation: imageAnimation 4s ease-in;
}
.image-div img {
  width: 100%;
  height: 100%;
}

.Button {
  background-color: #7180df;
  border: none;
  color: white;
  padding: 10px 40px;
  text-align: center;
  border-radius: 25px;
  font-size: 16px;
  font-weight: 500;
  margin-top: 10px;
  cursor: pointer;
  text-decoration: none;
}

/* Header slider  */

/* Slider image  */
.container-slider {
  max-width: 700px;
  height: 500px;
  margin: 100px auto 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.active-anim {
  opacity: 1;
}

.btn-slide {
  border-radius: 50%;
  position: absolute;
  top: 40%;
  margin: 0 0 0 10px;
}

.next-button {
  width: 60px;
  height: 60px;
  border: none;
  cursor: pointer;
  border-radius: 100%;
}
.btn-slide2 {
  border-radius: 50%;
  position: absolute;
  top: 40%;
  right: 0;
  margin: 0 10px 0 0;
}

.container-dots {
  position: absolute;
  bottom: 30px;
  right: 48%;
  /* transform: translateX(-50%); */
  display: flex;
  align-items: center;
}
.dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 3px solid #f1f1f1;
  margin: 0 5px;
  background: #f1f1f1;
}
.dot.active {
  background: rgb(32, 32, 32);
}

/* 
men image div styles
*/
.section-style {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.section-style .category-title {
  width: 90%;
  padding: 10px 0 30px 0;
}

.section-style .category-title h1 {
  color: #6a6867;
}

.men-initial-container {
  width: 90%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}
.men-containers {
  position: relative;
  border: 1px solid #b9b6b4;
  border-radius: 10px;
}

.men-containers:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  transition: box-shadow 0.3s ease-in-out;
}
.men-container {
  position: relative;
}

.image-icon {
  position: relative;
  width: 400px;
  height: 450px;
  overflow: hidden;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  border-radius: 10px;
}

.men-container .men-image-style {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  cursor: pointer;
}

.men-container img:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  overflow: hidden;
  border-radius: 10px;
}

.image-icon .icon-style {
  position: absolute;
  width: 100%;
  visibility: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  bottom: 2%;
  opacity: 0;
  -webkit-transform: translateY(2em);
          transform: translateY(2em);
  transition: 0s 3s;
  z-index: 1000;
}

.icon-style .cart-icon,
.fav-icon {
  cursor: pointer;
}

.men-image-style:hover + .icon-style {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
  transition: 1s;
}

.shop-now-button {
  margin: 30px;
  text-decoration: none;
}

.more-button {
  padding: 15px 80px;
  background-image: linear-gradient(
    to right,
    rgb(58, 187, 187),
    rgb(115, 218, 161)
  );
  font-weight: 700;
  margin: 50px 0 20px 0;
}

.men-container h3 {
  margin-top: 20px;
}
.men-container h3,
p {
  color: #413e3c;
  margin-left: 10px;
}
