/* Responsive screen */
/* for tablet  */
@media only screen and (min-width: 768px) and (max-width: 1023px) {
  /* menu area */
  nav .logo-menu .drawer {
    display: block;
  }
  nav .main-nav-menu ul,
  nav .Icons-view .tab-hide {
    display: none;
  }
  nav .drawer-navigation-active {
    width: 50%;
  }
  /* menu area */

  /* body style */

  .section-style .men-initial-container {
    /* width: 90%; */
    /* display: grid; */
    grid-template-columns: repeat(1, 1fr);
    /* grid-column-gap: 20px;
    grid-row-gap: 20px; */
  }
  .section-style .men-initial-container .men-container .image-icon {
    width: 100%;
    height: 100%;
  }
  /* body style */
  /* footer section */
  footer {
    display: block;
  }
  /* footer section */
}

/* for mobile  */
@media only screen and (max-width: 767px) {
  /* menu area */

  nav .logo-title {
    display: flex;
    flex-direction: column;
  }

  nav .logo-title span {
    font-size: 12px;
  }
  nav .logo-menu .drawer {
    display: block;
  }

  nav .drawer-navigation-active {
    width: 70%;
  }

  nav .main-nav-menu ul,
  nav .Icons-view .tab-hide,
  nav .main-nav-menu ul,
  nav .Icons-view .mobile-hide {
    display: none;
  }
  /* SSearch Bar */
  nav .Icons-view input[type="text"] {
    width: 80px;
  }

  nav .Icons-view input[type="text"]:focus {
    width: 100px;
  }

  nav .Icons-view ::-webkit-input-placeholder {
    font-size: 12px;
  }

  nav .Icons-view ::placeholder {
    font-size: 12px;
  }
  /* SSearch Bar */
  /* menu area */

  /* body style */
  .body-container {
    display: block;
  }
  .body-container .image-text {
    flex-direction: column;
  }

  .body-container .image-text-anime {
    flex-direction: column;
  }

  .body-container .text-div {
    /* display: none; */
    width: 100%;
    position: absolute;
    height: 80vh;
  }
  .body-container .image-div {
    width: 100%;
    height: 100vh;
  }
  .body-container .btn-slide,
  .body-container .btn-slide2,
  .body-container .container-dots {
    z-index: 1000;
  }

  .section-style .men-initial-container {
    grid-template-columns: repeat(1, 1fr);
  }
  .section-style .men-initial-container .men-container .image-icon {
    width: 100%;
    height: 100%;
  }
  /* body style */
  /* footer section */
  footer section .footer-sections {
    grid-template-columns: repeat(1, 1fr);
    /* border: 1px solid; */
  }
  /* footer section */
}

